import React, { Component } from "react";
import Sidebar from "react-sidebar";
import {
    NavLink
  } from "react-router-dom";

class SidebarComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }
 
  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
  }
  
  render() {
    return (
        <Sidebar
        sidebar={
          <>
          <button className="close" onClick={() => this.onSetSidebarOpen(false)} style={{float: 'right'}}>
        &#9776;
        </button>
        <div className="navbox">
          
          <h3>
            <NavLink
          className="navbar-item navlink"
          activeClassName="is-active"
          to="/home"
          onClick={() => this.onSetSidebarOpen(false)}
          exact
          >
            Home
          </NavLink>
          </h3>

          <h3>
            <NavLink
            className="navbar-item navlink"
            activeClassName="is-active"
            to="/biography"
            onClick={() => this.onSetSidebarOpen(false)}
            exact
            >
              Biography
            </NavLink>
          </h3>

          <h3>
            <NavLink
            className="navbar-item navlink"
            activeClassName="is-active"
            to="/timeline"
            onClick={() => this.onSetSidebarOpen(false)}
            exact
            >
              Timeline
            </NavLink>
          </h3>

          <h3>
            <NavLink
            className="navbar-item navlink"
            activeClassName="is-active"
            to="/memory"
            onClick={() => this.onSetSidebarOpen(false)}
            exact
            >
              Collective Memory
            </NavLink>
          </h3>

          <h3>
            <NavLink
            className="navbar-item navlink"
            activeClassName="is-active"
            to="/marvaandtate"
            onClick={() => this.onSetSidebarOpen(false)}
            exact
            >
              Marva and <br/> Horace Tate
            </NavLink>
          </h3>

          <h3>
            <NavLink
            className="navbar-item navlink"
            activeClassName="is-active"
            to="/references"
            onClick={() => this.onSetSidebarOpen(false)}
            exact
            >
              References
            </NavLink>
          </h3>

        </div>
          
          </>
        
        }
        open={this.state.sidebarOpen}
        onSetOpen={this.onSetSidebarOpen}
        pullRight={true}
        styles={{ sidebar: { background: "white", minWidth: '20%' } }}
      >
        <button className="open" onClick={() => this.onSetSidebarOpen(true)} style={{float: 'right'}}>
        &#9776;
        </button>
      </Sidebar>
    );
  }
}

export default SidebarComponent;
