import './App.css';
import React, { Component } from "react";
import {HomeComponent, TimelineComponent, SidebarComponent, BioComponent, MemoryComponent, ReferenceComponent, MarvaTateComponent} from "./components/index";
import { Routes } from "./navigation/routes";
import {
  HashRouter,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";

class App extends Component {

  render() {
    return (
    <HashRouter basename='/'>
       
        <SidebarComponent/>
      <Switch>
        <Route path={Routes.HOME} component={HomeComponent}/>
        <Route path={Routes.TIMELINE} component={TimelineComponent} />
        <Route path={Routes.BIO} component={BioComponent} />
        <Route path={Routes.MEMORY} component={MemoryComponent} />
        <Route path={Routes.REFERENCES} component={ReferenceComponent} />
        <Route path={Routes.MARVATATE} component={MarvaTateComponent} />
        <Route path="*">
          <Redirect to={Routes.HOME} />
        </Route>
    </Switch>

       <footer style={{paddingBottom: "10px"}}>
         <br/>
       <p>Created by Brittney Chin <br/> EDUC440A - Black Educator Narratives <br/> May 6, 2021</p>
       </footer>
      </HashRouter>
      
    );
  }
}

export default App;
